@font-face {
  font-family: "PT Mono";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../../fonts/ptmono-regular-webfont.woff2") format("woff2"), url("../../fonts/ptmono-regular-webfont.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../../fonts/robotocondensed-regular-webfont.woff2") format("woff2"), url("../../fonts/robotocondensed-regular-webfont.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../../fonts/robotocondensed-regular-webfont.woff2") format("woff2"), url("../../fonts/robotocondensed-regular-webfont.woff") format("woff"); }

html {
  background-color: #ffffff;
  color: #666666;
  font-size: 16px;
  line-height: 1.3;
  font-family: "PT Mono", "Arial", sans-serif; }

body {
  margin: 0;
  min-width: 320px; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.page-header {
  min-width: 1920px;
  margin: 0 auto;
  color: #ffffff;
  padding-top: 25px;
  background-image: url("../img/circuit-board.png"), url("../img/triangle.png"), url("../img/bg-banner.png");
  background-repeat: no-repeat, no-repeat, no-repeat;
  background-position: 85% 70%, 81% 70%, 0 0;
  background-color: #1b2d37; }

.page-header__wrapper {
  width: 1420px;
  padding-left: 250px;
  padding-right: 250px;
  margin: 0 auto;
  display: flex;
  flex-direction: column; }

@media (max-width: 1023px) {
  .page-header {
    min-width: 320px;
    margin: 0;
    padding-top: 17px;
    background-image: url("../img/circuit-board-tablet.png"), url("../img/triangles-tablet.png"), url("../img/bg-banner.png");
    background-position: 100% 32%, 100% 27%, 0 0; }
  .page-header__wrapper {
    width: 718px;
    padding-left: 25px;
    padding-right: 25px;
    margin: 0 auto;
    background-position: 100% 32%, 100% 27%, 0 0; } }

@media (max-width: 767px) {
  .page-header__wrapper {
    width: 292px;
    padding-left: 14px;
    padding-right: 14px;
    margin: 0 auto; }
  .page-header {
    background-image: url("../img/circuit-board-mob.png"), url("../img/triangles-mob.png"), url("../img/bg-banner.png");
    background-position: 57% 23%, 54% 27%, 0 0; } }

.logo__image {
  width: 210px;
  height: 55px; }

@media (max-width: 767px) {
  .logo__image {
    width: 38px;
    height: 38px; } }

.header-middle {
  margin-bottom: 278px;
  position: relative; }

@media (max-width: 1023px) {
  .header-middle {
    margin-bottom: 166px;
    margin-left: 59px; } }

@media (max-width: 767px) {
  .header-middle {
    margin-bottom: 44px;
    margin-left: 0; } }

.main-nav {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 225px; }

.main-nav__link {
  color: #ffffff; }

.main-nav__link-mail {
  display: block;
  padding-left: 35px;
  background-image: url("../img/phone-call.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
  margin-left: 94px; }

.main-nav__link-tel {
  display: block;
  padding-left: 35px;
  background-image: url("../img/envelope.svg");
  background-repeat: no-repeat;
  background-position: 0 0; }

.main-nav__link:hover,
.main-nav__link:focus {
  color: #00b2ff;
  opacity: 0.7; }

.main-nav__link:active {
  opacity: 0.5; }

.main-nav__order {
  margin-left: 45px; }

.main-nav__email {
  margin-left: 85px; }

@media (max-width: 1023px) {
  .main-nav {
    margin-bottom: 110px; }
  .main-nav__order {
    display: none; }
  .main-nav__link-tel {
    width: 26px;
    height: 28px;
    margin-left: 0;
    background-image: url("../img/phone-call-big.svg");
    padding-left: 31px;
    background-position: 100% 0; }
  .main-nav__link-mail {
    width: 26px;
    height: 28px;
    margin-left: 0;
    padding-left: 0;
    background-image: url("../img/envelope-big.svg");
    background-position: 100% 0; }
  .main-nav__text {
    display: none; } }

@media (max-width: 767px) {
  .main-nav {
    margin-bottom: 227px; } }

.logo-item {
  flex-grow: 1;
  margin-top: 7px; }

@media (max-width: 1023px) {
  .logo-item {
    margin-top: 4px;
    margin-left: -5px; } }

.button {
  border: 1px solid #ffffff;
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
  box-sizing: border-box;
  border-radius: 5px;
  padding: 15px;
  text-transform: uppercase;
  background-color: transparent;
  text-align: center;
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 1.1;
  letter-spacing: 0.05em;
  font-weight: bold;
  width: 185px; }

.button:hover,
.button:focus {
  background-color: #ffffff;
  color: #666666; }

.button:active {
  color: #1b2d37; }

.button--blue {
  width: 407px;
  background-color: #00b2ff;
  border: none;
  padding: 18px; }

@media (max-width: 1023px) {
  .button--blue {
    width: 379px; } }

@media (max-width: 767px) {
  .button--blue {
    width: 287px; } }

.page-title {
  margin: 0;
  margin-bottom: 34px;
  padding: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 80px;
  line-height: 1.1;
  letter-spacing: 0.07em; }

@media (max-width: 1023px) {
  .page-title {
    width: 370px;
    font-size: 60px;
    margin-bottom: 34px; } }

@media (max-width: 767px) {
  .page-title {
    width: 222px;
    font-size: 40px; } }

.page-promo {
  margin: 0;
  padding: 0;
  font-size: 22px;
  line-height: 1.5;
  width: 638px;
  padding-left: 96px;
  position: relative;
  margin-bottom: 40px; }

.page-promo::before {
  content: "";
  width: 66px;
  height: 2px;
  background-color: #ffffff;
  position: absolute;
  left: 0;
  top: 13px; }

@media (max-width: 1023px) {
  .page-promo {
    padding-left: 50px;
    width: 356px;
    font-size: 18px;
    margin-bottom: 56px; }
  .page-promo::before {
    width: 33px; } }

@media (max-width: 767px) {
  .page-promo {
    width: 236px;
    font-size: 16px;
    margin-bottom: 42px; } }

.link-to-consultation {
  margin-left: 96px; }

.link-to-consultation__text--mobile {
  display: none; }

@media (max-width: 1023px) {
  .link-to-consultation {
    margin-left: 50px; } }

@media (max-width: 767px) {
  .link-to-consultation {
    margin-left: 0; }
  .link-to-consultation__text {
    display: none; }
  .link-to-consultation__text--mobile {
    display: inline; } }

.scroll-link {
  position: absolute;
  color: #ffffff;
  opacity: 0.5;
  top: 415px;
  left: -42px;
  transform: rotate(-90deg); }

.scroll-link:hover,
.scroll-link:focus {
  color: #00b2ff; }

.scroll-link:active {
  color: #ffffff;
  opacity: 0.3; }

@media (max-width: 1023px) {
  .scroll-link {
    display: none; } }
